.header{background-color:#222;height:60px;padding:20px;color:white;-webkit-box-shadow:2px 2px 3px 2px rgba(0,0,0,0.1);box-shadow:2px 2px 3px 2px rgba(0,0,0,0.1);position:fixed;top:0;width:100%;z-index:1000}.header__logo-type{width:50%;height:100%;display:inline-block;vertical-align:middle}.header__logo-type__logo{-webkit-animation:Viewer-logo-spin infinite 3.5s linear;animation:Viewer-logo-spin infinite 3.5s linear;float:left;background-image:url("images/logo-elem.gif");width:60px;height:60px;background-size:contain;background-position:0 1px;background-repeat:no-repeat}.header__logo-type__logo:hover{-webkit-animation:Viewer-logo-spin infinite 1.5s linear;animation:Viewer-logo-spin infinite 1.5s linear}.header__logo-type__logo_size_small{height:60px}.header__logo-type__title{float:left;margin:15px 0 0 15px}.header__logo-type__title img{max-width:200px;height:auto}.header__theme-switcher{width:300px;position:absolute;right:20px;top:20px}@-webkit-keyframes Viewer-logo-spin{from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes Viewer-logo-spin{from{-webkit-transform:rotate(360deg);transform:rotate(360deg)}to{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}



